.welcomeContainer {
    margin: 0;
    font-weight: 500;
    font-family: 'HelveticaNeue';
    box-sizing: border-box;
}

section {
    width: 100%;
    padding: 0 7%;
    display: table;
    margin: 0;
    max-width: none;
    background: #023246;
    height: 100vh;
}
section:nth-of-type(2n) {
    background-color: #FE4B74;
}

.intro {
    height: 100vh;
}

.content {
    display: table-cell;
    vertical-align: middle;
}

.title {
    font-size: 3em;
    display: block;
    color: white;
    font-weight: 300;
}

p {
    font-size: 1.5em;
    font-weight: 500;
    color: #C3CAD9;
}

a {
    font-weight: 700;
    color: #373B44;
    position: relative;
}
a:hover {
    opacity: 0.8;
}
a:active {
    top: 1px;
}

footer {
    padding: 1% 5%;
    text-align: center;
    background-color: #373B44;
    color: white;
}
footer a {
    color: #FE4B74;
    font-weight: 500;
    text-decoration: none;
}
